import styled from 'styled-components';
import {Link} from 'react-scroll';
import {ReactLogo} from '@styled-icons/boxicons-logos/ReactLogo';
import {Bars} from '@styled-icons/fa-solid/Bars';

export const StyledNavbar = styled.nav`
  background: ${({scrollNav}) => (scrollNav ? '#1c1b1b':'black')};
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 10;
  font-weight: bold;
`;

export const NavbarContainer = styled.div`
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 24px;
    max-width: 1500px;
`;

export const NavLogo = styled(Link)`
    color: #fff;
    justify-content: flex-start;
    cursor: pointer;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-left: 24px;
    font-weight: bold;
    text-decoration: none;
`;

export const NavIcon = styled(ReactLogo)`
  margin-left: 0.5rem;
  font-size: 3px;
`;

export const MenuIcon = styled(Bars)`
  color: #fff;
  height: 80px;
  display: none;

  @media screen and (max-width: 768px) {
    right: 20px;
    display: block;
    position: absolute;
    cursor: pointer;
    color: #fff;
  }
`;

// Sidebar CSS *Appears when menu bar is clicked
export const NavMenu = styled.ul`
  color: #fff;
  height: 80px;
  justify-content: space-between;
  align-items: center;
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;

  @media screen and (max-width: 768px) {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* height: 90vh; */
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 5.5s ease;
  }
`;

export const NavItem = styled(Link)`
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  cursor: pointer;

  &.active {
    border-bottom: 3px solid white;
  }
`;