import styled from 'styled-components';

export const StyledAbout = styled.section`
  padding: 3rem;
  background: #f5f5f5;
  z-index: 1;  
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const AboutH1 = styled.h1`
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 5rem;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
`;

export const Headers = styled.h1`
  color: #444649;
  font-size: 2rem;
  text-decoration: underline;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  text-align: center;
`;

//Values section (called Points in code)
export const AboutWrapper = styled.div`
  width: 100%;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  align-items: center;
  grid-gap: 16px;
  padding: 0 50px;
`;

export const PointsWrapper = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  display: grid;
  align-items: center;
  grid-gap: 16px;

  /* Items separated into 3 columns */
  grid-template-columns: 1fr 1fr 1fr;

  /* Down to 2 columns on smaller screen */
  @media screen and (max-width: 768px) {
    grid-template-columns: 1fr 1fr;
  }

  /* Only 1 item showing on smaller screens */
  @media screen and (max-width: 480px) {
    grid-template-columns: 1fr;
    padding: 0 20px;
  }
`;

export const Points = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  height: 250px;

  @media screen and (max-width: 480px) {
    height: 170px;
  }
`;

export const PointsH2 = styled.h2`
  font-size: 2rem;
  height: 80px;
  color: #fff;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color:#1b8bb8;

  @media screen and (max-width: 480px) {
   font-size: 1.7rem;
  }
`;

export const PointsP = styled.p`
  font-size: 1.3rem;
  text-align: center;

  @media screen and (max-width: 480px) {
   font-size: 1rem;
  }
`;

//Bar separating Top and Bottom sections
export const Bar = styled.div`
  background: #444649;
  opacity: 0.5;
  height: 4px;
  border-radius: 3rem;
  margin-bottom: 1.3rem;
`;

//Info Section
export const InfoWrapper = styled.div`
  width: 100%;
  /* justify-content: center; */
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 16px;

  @media screen and (max-width: 768px) {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
  }
`;

//Skills (Left side of Info)
export const SkillsWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;

export const SkillsH1 = styled.h1`
text-align: center;
color: #444649;
font-size: 2rem;
text-decoration: underline;
text-align: center;

@media screen and (max-width: 480px) {
  font-size: 1.5rem;
}
`

export const IconWrapper = styled.div`
  justify-content: center;
  display: grid;
  margin-top: 20px;
  margin-left: 10px;
  grid-template-columns: 60px 60px;
  grid-template-rows: 60px 60px 60px 60px;

  @media screen and (max-width: 768px) {
    padding-top: 20px;
    grid-template-columns: 100px 100px 100px 100px;
  }
`;

//Bio (Right side of Info)
export const BioWrapper = styled.div`
  text-align: center;
  flex-direction: row;
  justify-content: center;
  align-items: center;
`;  

export const BioImg = styled.img`
  border: 5px solid rgba(0, 255, 234, 0.575);
  border-radius: 5rem;
  width: 70%;
  height: auto;
  @media screen and (max-width: 768px) {
   width:100%;
  }
`

export const BioSpan = styled.span`
  color:#1b8bb8;
  text-decoration: underline;
  cursor: pointer;
`

export const BioText = styled.p`
  text-align: left;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
`